function getKnowledgeSearch(params) {
	return $helper()
		.get(`api/knowledges`, { params })
		.then((res) => {
			return res.data.data;
		});
}

// Ëé∑ÂèñÂÜÖÂÆπËØ¶ÊÉÖ
function getKnowledgeDetail(id) {
	return $helper()
		.get(`api/wiki/post`, {
			params: {
				source_id: id,
				type: "knowledge",
			},
		})
		.then((res) => {
			return res.data.data;
		});
}

// Ëé∑ÂèñÁâàÊú¨
function getKnowledgePost(id) {
	return $helper()
		.get(`api/wiki/post/${id}`)
		.then((res) => {
			return res.data.data;
		});
}

